.Card3 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 70px);
  grid-gap: 10px;
  justify-items: stretch;
  align-items: center;

  margin-top: 50px;
}

.Card3-logo {
  width: 450px;
}

.Card3 > button:nth-child(1) {
  grid-column: 2;
  grid-row-start: 3;
  justify-content: center;
}
.Card3 > button:nth-child(2) {
  grid-column: 3;
  grid-row-start: 1;
}
.Card3 > button:nth-child(3) {
  grid-column: 3;
  grid-row-start: 2;
}
.Card3 > button:nth-child(4) {
  grid-column: 3;
  grid-row-start: 3;
}
.Card3 > button:nth-child(5) {
  grid-column: 3;
  grid-row-start: 4;
}
.Card3 > button:nth-child(6) {
  grid-column: 3;
  grid-row-start: 5;
}
.Card3 > button:nth-child(7) {
  grid-column: 3;
  grid-row-start: 6;
}
