.Card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* grid-template-rows: repeat(6, 70px); */
  grid-gap: 10px;
  justify-items: stretch;
  align-items: center;

  margin-top: 15px;
}

.Card-logo {
  width: 250px;
}

.Card > button:nth-child(1) {
  grid-column: 1;
  grid-row-start: 3;
  justify-content: center;
}
.Card > button:nth-child(2) {
  grid-column: 2;
  grid-row-start: 3;
}
.Card > button:nth-child(3) {
  grid-column: 3;
  grid-row-start: 3;
}
.Card > button:nth-child(4) {
  grid-column: 4;
  grid-row-start: 1;
}
.Card > button:nth-child(5) {
  grid-column: 4;
  grid-row-start: 2;
}
.Card > button:nth-child(6) {
  grid-column: 4;
  grid-row-start: 3;
}
.Card > button:nth-child(7) {
  grid-column: 4;
  grid-row-start: 4;
}
.Card > button:nth-child(8) {
  grid-column: 4;
  grid-row-start: 5;
}
.Card > button:nth-child(9) {
  grid-column: 4;
  grid-row-start: 6;
}
.Card > button:nth-child(10) {
  grid-column: 1;
  grid-row-start: 1;
}
.Card > button:nth-child(11) {
  grid-column: 1;
  grid-row-start: 2;
}
