.Card4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 70px);
  grid-gap: 10px;
  justify-items: stretch;
  align-items: center;

  margin-top: 50px;
}

.Card4-logo {
  width: 450px;
}

.Card4 > button:nth-child(1) {
  grid-column: 2;
  grid-row-start: 2;
  justify-content: center;
}
.Card4 > button:nth-child(2) {
  grid-column: 3;
  grid-row-start: 1;
}
.Card4 > button:nth-child(3) {
  grid-column: 3;
  grid-row-start: 3;
}
